<template>
  <div>
    <a
      class="custom-button"
      @click="toggleEditView"
      v-show="showEditView"
    >
     Back
    </a>
    <router-link
      tag="button"
      class="mdl-button mdl-js-button mdl-button--raised mdl-js-ripple-effect mdl-button--colored custom-button custom-button--right custom-button--primary"
      :to="{ name: 'vendors.create' }"
    >
      <i class="material-icons">add</i> Add
    </router-link>
    <div class="mdl-layout-spacer"></div>

    <data-table
      :columns="columns"
      :data="vendors.data"
      :actionMethod="fetchVendors"
      :count="vendors.count"
      @rowSelected="editVendor"
      v-show="!showEditView"
    ></data-table>
    <vendor-edit-form
      :vendor="vendor"
      v-show="showEditView"
    ></vendor-edit-form>
  </div>
</template>
<script>
import { mapActions, mapGetters } from 'vuex'
import { DataTable } from 'SHARED/data_table'
import { VendorEditForm } from '.'
export default {
  name: 'vendors-list-view',
  components: {
    DataTable,
    VendorEditForm
  },
  computed: {
    ...mapGetters({
      vendors: 'getVendors'
    })
  },
  mounted () {
    window.componentHandler.upgradeAllRegistered()
  },
  methods: {
    ...mapActions([
      'fetchVendors'
    ]),
    editVendor (vendor) {
      this.vendor = vendor
      this.showEditView = true
    },
    toggleEditView () {
      this.showEditView = !this.showEditView
    }
  },
  data () {
    return {
      columns: [
        {
          field: 'name',
          label: 'Name',
          type: String
        },
        {
          field: 'gst_number',
          label: 'GST Number',
          type: String
        },
        {
          field: 'email',
          label: 'Email',
          type: String
        },
        {
          field: 'balance_amount',
          label: 'Pending Amount',
          type: Number
        },
        {
          field: 'is_active',
          label: 'Active',
          type: Boolean
        }
      ],
      showEditView: false,
      vendor: {}
    }
  }
}
</script>



